	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/images/smhtree1202.jpg\" border=\"0\" alt=\"Tree Killer\" /></p>\n<p>I was hoping to have been able to tell the entire story of harvesting a Christmas tree from our front yard, but alas, our tree stand is too small, so we'll have to wait until Monday to bring the tree inside. </p>\n<p>When we first moved out of the city, Mama used to always bring a small shovel along on our bike rides to \"liberate\" baby pine trees from the paths at the <a href=\"http://www.mu.niedersachsen.de/Aktuell/naturparke/heide.htm\" title=\"Naturpark Lüneburger Heide\">nearby nature reserve</a>, where they would have been tramped to death by tourists, to be planted in our garden. This tree is the last of those liberated trees. It must be about 8 years old.</p>","fields":{"title":"Tree Killer","date":"14 Dec 2002","url":"https://papascott.de/2002-12-14-tree-killer/"}}},"pathContext":{"slug":"/2002-12-14-tree-killer/","prev":{"fields":{"title":"Let's Make a Deal!","date":"14 Dec 2002","slug":"/2002-12-14-lets-make-a-deal/"}},"next":{"fields":{"title":"Buying Cheese","date":"15 Dec 2002","slug":"/2002-12-15-buying-cheese/"}}}}

